
  import {message} from 'ant-design-vue';
  import {defineComponent, ref} from 'vue';

  export default defineComponent({
    name: 'CodeBox',
    props: {
      code: String,
      markdown: String
    },
    setup(props) {
      const showCode = ref(false);
      const copied = () => {
        message.success('已复制');
      };
      return {
        showCode,
        copied
      };
    }
  });
